.chapter-default {
  padding: 16px;
  border-radius: 6px;
  margin: 12px;
  color: black; }
  .chapter-default a {
    color: black; }
    .chapter-default a:hover {
      text-decoration: none;
      color: rgba(0, 0, 0, 0.8); }
  .chapter-default.glow {
    box-shadow: 0 0 18px black;
    -webkit-box-shadow: 0 0 18px 0 black;
    -moz-box-shadow: 0 0 18px 0 black;
    border: 2px solid black; }
  .chapter-default.solid {
    border: 2px solid black; }

